@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@200;300;400;600;700;800;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@200;300;400;600;700;800;900&family=Poppins:wght@300;400;500;600;700;800&display=swap);
* {
  margin: 0;
  border: none;
  overflow-x: hidden;
}

* {
  --background-color: #454545;
  --highlight-color-green: #1DB954;
  --highlight-color-blue: #4791FF;
  --highlight-color-dark-green: #228947;
}

* {
  font-family: 
  'Poppins',
  sans-serif;
}

body {
  font-family: 'Poppins', sans-serif;
  font-weight: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1,
h2, 
h3 {
  font-family: 'Nunito', sans-serif;
  font-weight: bold;
}
p {
  font-family: 
  'Poppins',
  sans-serif;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
    min-height: 100vh;
    width: 100vw;

    background: linear-gradient(to bottom right, var(--background-color), var(--highlight-color-dark-green));

    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;

}

button:hover{
    -webkit-filter: brightness(1.2);
            filter: brightness(1.2);
}
button:active {
    -webkit-transform: scale(0.95);
            transform: scale(0.95);
}
@-webkit-keyframes from-up {
    from {
        -webkit-transform: translateY(-100px);
                transform: translateY(-100px);
        opacity: 0;
    }   
    to {
        -webkit-transform: translateY(0);
                transform: translateY(0);
        opacity: 1;
    }
}

@keyframes from-up {
    from {
        -webkit-transform: translateY(-100px);
                transform: translateY(-100px);
        opacity: 0;
    }   
    to {
        -webkit-transform: translateY(0);
                transform: translateY(0);
        opacity: 1;
    }
}

@-webkit-keyframes from-down {
    from {
        -webkit-transform: translateY(50px);
                transform: translateY(50px);
        opacity: 0;
    }   
    to {
        -webkit-transform: translateY(0);
                transform: translateY(0);
        opacity: 1;
    }
}

@keyframes from-down {
    from {
        -webkit-transform: translateY(50px);
                transform: translateY(50px);
        opacity: 0;
    }   
    to {
        -webkit-transform: translateY(0);
                transform: translateY(0);
        opacity: 1;
    }
}

@-webkit-keyframes from-up-light {
    from {
        -webkit-transform: translateY(-30px);
                transform: translateY(-30px);
        opacity: 0;
    }   
    to {
        -webkit-transform: translateY(0);
                transform: translateY(0);
        opacity: 1;
    }
}

@keyframes from-up-light {
    from {
        -webkit-transform: translateY(-30px);
                transform: translateY(-30px);
        opacity: 0;
    }   
    to {
        -webkit-transform: translateY(0);
                transform: translateY(0);
        opacity: 1;
    }
}
.Card {
    width: 90%;
    max-width: 40rem;
    background: linear-gradient(to bottom left, rgba(201, 201, 201, 0.3), rgba(122, 122, 122, 0.2));
    border-radius: 2rem;

    min-height: 10rem;
    max-width: 30rem;

    margin: 5vh 2rem 0 2rem;

    -webkit-filter: drop-shadow(10px 10px 10px rgba(0, 0, 0, 0.6));

            filter: drop-shadow(10px 10px 10px rgba(0, 0, 0, 0.6));

    -webkit-animation-name: from-down;

            animation-name: from-down;
    -webkit-animation-duration: 3s;
            animation-duration: 3s;

    transition: 0.5s;
}

.card-inner-container {
    margin: 2rem;

    text-align: center;
}
.DateSelector {
    margin: 2rem auto;
    
    display: flex;
    flex-direction: column;
}

.form-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.info {
    width: 25px;
    height: 25px;
    border-radius: 50%;

    text-align: center;
    color: white;
    background-color: var(--background-color);

    cursor: pointer;
    -webkit-filter: drop-shadow(3px 3px 5px rgba(0, 0, 0, 0.6));
    filter: drop-shadow(3px 3px 5px rgba(0, 0, 0, 0.6));

    margin: 3px 10px 10px 3px;
}

.date-selector-form {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;

    min-height: 10rem;
}

.date-selector {
    height: 2.4rem;
    width: 10rem;
    padding: 0 1rem;
    margin: 2rem auto 0 auto;

    border-radius: 3rem;
    -webkit-filter: drop-shadow(5px 5px 5px rgb(0, 0, 0));
            filter: drop-shadow(5px 5px 5px rgb(0, 0, 0));

    font-family: 
        'Poppins',
        sans-serif;

    font-size: 0.7rem;
    cursor: pointer;
}

.button-fill {
    display: flex;
    align-items: center;
    justify-content: center;

    height: 2.5rem;
    width: 15rem;
    margin: 2rem auto;
    border: none;
    border-radius: 10rem;
    background-color: var(--highlight-color-green);

    -webkit-filter: drop-shadow(5px 5px 7px rgba(0, 0, 0, 0.6));

            filter: drop-shadow(5px 5px 7px rgba(0, 0, 0, 0.6));
    transition: 0.2s;
    cursor: pointer;

    color: black;
    font-family: 
        'Poppins',
        sans-serif;
    font-size: 1rem;
    font-weight: bold;  

}





.button-fill:hover,
.info:hover
{
    -webkit-filter: brightness(1.2);
            filter: brightness(1.2);
}
.button-fill:active,
.info:active
{
    -webkit-transform: scale(0.95);
            transform: scale(0.95);
}


.InfoBubble {
    background-color: rgb(21, 21, 21);
    border-radius: 3rem;

    width: 15rem;
    z-index: 2;

    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);

    padding: 1rem;

    -webkit-filter: drop-shadow(8px 8px 15px rgba(0, 0, 0, 0.9));

            filter: drop-shadow(8px 8px 15px rgba(0, 0, 0, 0.9));
}

.info-title{
    color: var(--highlight-color-green);
}
.info-text{
    color: white;
    font-size: 0.7rem;
    margin: 1rem auto;
    font-weight: lighter;
}
.info-text a {
    color: white;
}

* {
    --loader-size: 20px;
    --loader-width: 3px;
}

.Loader {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    overflow: hidden;
    margin-top: 1rem;
    width: 100%;
}

.circle {
    height: var(--loader-size);
    width: var(--loader-size);

    background: none;

    border-style: solid;
    border-radius: 50%;
    border-width: var(--loader-width);
    border-color: var(--highlight-color-green);
    border-left-color: transparent;

    -webkit-animation-name: spin;

            animation-name: spin;
    -webkit-animation-duration: 1000ms;
            animation-duration: 1000ms;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-timing-function: cubic-bezier(.35,.62,.87,.43);
            animation-timing-function: cubic-bezier(.35,.62,.87,.43); 
}

.Loader p {
    font-size: 0.8rem;
    margin-top: 1rem;
}

@-webkit-keyframes spin {
    from {
        -webkit-transform:rotate(0deg);
                transform:rotate(0deg);
    }
    to {
        -webkit-transform:rotate(360deg);
                transform:rotate(360deg);
    }
}

@keyframes spin {
    from {
        -webkit-transform:rotate(0deg);
                transform:rotate(0deg);
    }
    to {
        -webkit-transform:rotate(360deg);
                transform:rotate(360deg);
    }
}
.Results {
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: column;

    -webkit-animation-name: from-up-light;

            animation-name: from-up-light;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
}

.playlist-name {
    font-size: 1rem;
    font-weight: bold;
    margin: 1rem auto;
}

.uri {
    margin: 1rem auto;
    max-height: 5rem;
    max-width: 100%;
    border-radius: 2rem;
}

.listen-button, 
.again-button
{
    height: 2rem;

    margin: 1rem auto;

    border-color: var(--highlight-color-green);
    border-radius: 5rem;
    border-width: 3px;

    font-weight: bolder;
    font-size: 1rem;

    transition: 0.3s;
}


.listen-button{
    width: 13rem;
    border: none;
    background-color: var(--highlight-color-green);
    color: black;
}
.again-button {
    width: 8rem;
    font-size: 0.8rem;
    border-style: solid;
    background-color: var(--background-color);
    color: var(--highlight-color-green);
    margin: 3rem auto;

}
.Logo {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 2rem auto;

    -webkit-animation-name: from-up;

            animation-name: from-up;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
}

.Logo * {
    -webkit-filter: drop-shadow(5px 5px 7px rgba(0, 0, 0, 0.6));
    filter: drop-shadow(5px 5px 7px rgba(0, 0, 0, 0.6));

}

.logo-image {
    height: 4rem;
    margin: 1rem;
}

.name {
    height: 2rem;
    margin: auto 1rem;
}
.Footer{
    width: 100vw;
    min-height: 2rem;
    background-color: black;

    position: absolute;
    top: 100vh;
    right: 0;

    display: flex;
    justify-content: space-between;
    align-items: center;

    margin: 0;
}

.Footer p {
    color: white;
    font-size: 0.8rem;
    margin: auto 0 auto 5vw;
}

.links {
    margin: 5px 5vw 3px 0;
}

.links * {
    height: 1.7rem;
    margin: 5px;
    transition: 0.2s;
}

.links *:hover {
    -webkit-transform: translateY(-5px);
            transform: translateY(-5px);
}
